import React from "react";
import VimeoAPI from "@vimeo/player";

const checkWindow = (typeof window === `undefined`) ? false : true; // gatsby cannot have window called while building the pages

let screenSize;

const setScreenDimensions = () => {
  screenSize = {
    w: checkWindow ? window.innerWidth : 0,
    h: checkWindow ? window.innerHeight : 0
  }
}

if(checkWindow){
  window.addEventListener("resize", setScreenDimensions);
}

export default class VimeoEl extends React.Component{
  constructor(data){
    super();

    this.state = {
      loading: true,
      muted: true,
    }

    this.player = {
      el: React.createRef(),
      class: data.className,
      options: {
        ...data.vimeoOptions, 
        ...{ muted: true, autopuase: false }
      },
      clickPaused: false,
      scrollPaused: false,
      scrollPlay: false,
      muteBtn: React.createRef(),
      soundIconColor: data.soundIcon,
    }
  }

  componentDidMount(){
    this.init();
  }

  componentWillUnmount(){
    if(checkWindow){
      window.removeEventListener("scroll", this.player.scrollEvent);
      window.removeEventListener("resize", this.player.resizeEvent);
    }
    
    this.player.video.destroy();
    // // $(".-animating").off("animationend");
  }

  init(){
    this.player.video = new VimeoAPI(this.player.el.current, this.player.options);
    this.player.video.setVolume(0);

    if(screenSize === undefined){
      screenSize = {
        w: checkWindow ? window.innerWidth : 0,
        h: checkWindow ? window.innerHeight : 0
      }
    }

    this.player.resizeEvent = this.onResize.bind(this);

    if(checkWindow){
      window.addEventListener("resize", this.player.resizeEvent);
    }
    
    this.player.video.on("play",() => {
      if(!this.player.scrollPlay){
        if(this.player.options.background !== 1 && screenSize.w <= 1024){
          this.player.video.setVolume(1);
          this.setState({muted: false});
        }

        this.player.clickPaused = false;
        this.player.scrollPause = false;
      }

      if(this.props.videoPlayingCallback) this.props.videoPlayingCallback();
    });

    this.player.video.on("pause",() => {
      if(this.checkIfVideoInView()){ // if not paused by scrolling
        this.player.clickPaused = true;
        this.player.scrollPlay = false;
      }
    });

    this.player.video.ready().then(() => {
      if(this.player.el.current === null) return;

      this.setState({ loading: false });
      this.setVideoPositionDetails();

      if(this.props.videoReadyCallback) this.props.videoReadyCallback();
    })
  }

  onResize(){
    if(this.player.el.current){ // if video is on the page
      this.setVideoPositionDetails();
    }
  }

  onScroll(){
    if(screenSize.w > 1024){
      window.requestAnimationFrame(() => {
        this.screenTop = window.pageYOffset;
        this.screenBottom = this.screenTop + screenSize.h;

        if(this.checkIfVideoInView() && !this.player.clickPaused && !this.player.scrollPlay){
          this.player.video.play().catch(err => {
            console.log('err',err);
          });

          setTimeout(() => {
            this.player.scrollPlay = true;
            this.player.scrollPause = false;
          },100);
        } else if(!this.checkIfVideoInView() && !this.player.clickPaused && this.player.scrollPlay) {
          this.player.video.pause().catch(err => {
            console.log('err',err);
          });
          
          setTimeout(() => {
            this.player.scrollPlay = false;
            this.player.scrollPause = true;
          },100);
        }
      })
    }
  }

  setVideoPositionDetails(){
    this.player.videoBox = this.player.el.current.getBoundingClientRect();
    this.player.videoTop = (checkWindow ? window.pageYOffset : 0) + this.player.videoBox.top;
    this.player.videoBottom = this.player.videoTop + this.player.videoBox.height;

    // if($("section").hasClass("-firstLoad")){
    //   video.videoTop -= this.windowH * 0.2; // to account for the slideup if video loads beforehand
    // }

    this.player.scrollEvent = this.onScroll.bind(this);

    if(checkWindow){
      window.addEventListener("scroll", this.player.scrollEvent);
    }
  }

  checkIfVideoInView(){
    let videoTopQuater = this.player.videoTop + (this.player.videoBox.height * 0.25),
        videoBottomQuater = this.player.videoTop + (this.player.videoBox.height * 0.75);

    if(this.screenTop > videoBottomQuater || this.screenBottom < videoTopQuater){
      return false;
    } else {
      return true;
    }
  }

  muteClicked(){
    if(this.state.muted){
      this.player.video.setVolume(1);
      this.setState({
        muted: false
      })
    } else {
      this.player.video.setVolume(0);
      this.setState({
        muted: true
      })
    }
  }

  render() {
    const player = this.player;
    let iconColor = "";

    if(player.soundIconColor === "black"){
      iconColor = "-dark";
    }

    return (
      <div ref={player.el} key={player.options.id} className={`${player.class} ${this.state.loading ? "-loading" : ""}`}>
        {player.options.background !== true && (
          <div 
            ref={player.muteBtn} 
            onClick={() => this.muteClicked()}
            className={ `vimeo__mute ${iconColor} ${this.state.muted ? "" : "-sound"}` }
          />
        )}
      </div>
    );
  }
}